export default {
	onLoad: function () {
	    console.log('onLoad')
	    var that = this
	  },
	data() {
		var cxt_arc
		var timer
		var n=1
		var which = 1 //中奖项
		var luck = ["5积分", "5金币", "10积分", "10金币", "谢谢参与", "20金币"] //定义奖项
		return {
			animationData:{},//动画
			isclick: "start",//按钮事件
			image:"../../static/banner/banner3@2x.png",//转盘图片
			hiddenModal:true,//弹框是否隐藏
			detail: "恭喜您获得"+luck[this.which-1]//弹框内容
		}
	},
	methods:{
		start(e) {
		    var _this = this;
		    let n=1;
		    
		    this.isclick = "stop"
		  
		    reset.call(_this);
		    var timer = setInterval(function () {
		      //开始旋转
		      star.call(_this);
		      //
		      n++;
		    }
		      , 300);
		     //启动动画
		    function star() {
		      console.log("开始动画.....")
		      var animation = wx.createAnimation({
		        transformOrigin: "50% 50%",
		        duration: 300,
		        timingFunction: "linear"
		      });
		      animation.rotate(360*n).step();
		      this.animationData = animation.export()
		    }
		    //重置动画
		    function reset() {
		      console.log("重置动画.....")
		      var animation = wx.createAnimation({
		        transformOrigin: "50% 50%",
		        duration: 0,
		        timingFunction: "linear"
		      });
		      animation.rotate(0).step();
		      this.animationData = animation.export()
		    }
		},
		stop: function (e) {
		    var _this = this;
		    this.isclick = ""
		    clearInterval(timer);
		    timer = null;
		    console.log("结束动画.....");
		    sto.call(_this);
		    function sto() {
		      console.log("重置动画.....")
		      var animation = wx.createAnimation({
		        transformOrigin: "50% 50%",
		        duration: 1.4 * (2160 - (this.which - 1) * 60),
		        timingFunction: "ease-out"
		      });
		      console.log(Math.random()*60); 
		      animation.rotate(360 * n + (2160 - ((this.which-2) * 60) - 35 - (Math.random() * 50))).step();
		      this.animationData = animation.export(),
		      this.image = "/images/dianji_choujiang.png"
		    }
		 
		    let timer = setTimeout(function () {
		      _this.hiddenModal = false
		    }, 1.4 * (2160 - (this.which - 1) * 60+300))
		},
		listenerConfirm: function (e) {
		      var _this = this;
		      this.hiddenModal = true,
		      this.isclick = "start"

		      reset.call(_this);
		      function reset() {
		        console.log("重置动画.....")
		        var animation = wx.createAnimation({
		          transformOrigin: "50% 50%",
		          duration: 0,
		          timingFunction: "linear"
		        });
		        animation.rotate(0).step();
		        this.animationData = animation.export()
		      }
		}
	}
}
